"use client";

import { useState, useEffect } from "react";
import { updateContent } from "@/services/content.service";
import { getCategories } from "@/services/category.service";

interface EditContentFormProps {
  content: any;
  onSuccess: () => void;
  onCancel: () => void;
}

export default function EditContentForm({
  content,
  onSuccess,
  onCancel,
}: EditContentFormProps) {
  const [title, setTitle] = useState(content.title ?? "");
  const [description, setDescription] = useState(content.description ?? "");
  const [type, setType] = useState(content.type ?? "movie");
  const [year, setYear] = useState(content.year ?? "");
  const [loading, setLoading] = useState(false);

  const [cardImage, setCardImage] = useState<File | null>(null);
  const [posterImage, setPosterImage] = useState<File | null>(null);

  // Ø§Ù„ØªØµÙ†ÙŠÙØ§Øª
  const [categoriesList, setCategoriesList] = useState<any[]>([]);
  const [selectedCategories, setSelectedCategories] = useState<number[]>(
    content.categories?.map((c: any) => c.id) || []
  );

  const types = [
    "movie",
    "series",
    "program",
    "play",
    "kids",
    "podcast",
    "competition",
  ];

  const typesMap: Record<string, string> = {
    movie: "Ø£ÙÙ„Ø§Ù…",
    series: "Ù…Ø³Ù„Ø³Ù„Ø§Øª",
    program: "Ø¨Ø±Ø§Ù…Ø¬",
    play: "Ù…Ø³Ø±Ø­ÙŠØ§Øª",
    kids: "Ø£Ø·ÙØ§Ù„",
    podcast: "Ø¨ÙˆØ¯ÙƒØ§Ø³Øª",
    competition: "Ù…Ø³Ø§Ø¨Ù‚Ø§Øª",
  };

  useEffect(() => {
    const fetchCategories = async () => {
      try {
        const res = await getCategories();
        const cats = Array.isArray(res.data.data) ? res.data.data : [];
        setCategoriesList(cats);
      } catch (err) {
        console.error("Fetch categories error:", err);
      }
    };
    fetchCategories();
  }, []);

  const handleCategoryChange = (id: number, checked: boolean) => {
    if (checked) {
      setSelectedCategories((prev) => [...prev, id]);
    } else {
      setSelectedCategories((prev) => prev.filter((c) => c !== id));
    }
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setLoading(true);

    try {
      const formData = new FormData();

      formData.append("_method", "PUT"); // Ù…Ù‡Ù…
      formData.append("title", title);
      formData.append("description", description);
      formData.append("type", type);
      formData.append("year", String(year));

      selectedCategories.forEach((id) =>
        formData.append("category_ids[]", String(id))
      );

      if (cardImage) formData.append("card_image", cardImage);
      if (posterImage) formData.append("poster_image", posterImage);

      await updateContent(content.uuid, formData);

      setCardImage(null);
      setPosterImage(null);

      onSuccess();
    } catch (err) {
      console.error("Update failed", err);
      alert("ÙØ´Ù„ ØªØ¹Ø¯ÙŠÙ„ Ø§Ù„Ù…Ø­ØªÙˆÙ‰ØŒ Ø­Ø§ÙˆÙ„ Ù…Ø±Ø© Ø£Ø®Ø±Ù‰");
    } finally {
      setLoading(false);
    }
  };

  return (
    <form
      onSubmit={handleSubmit}
      className="bg-gray-800 p-4 rounded-lg shadow-lg flex flex-col gap-4 text-right"
    >
      {/* Ø§Ù„Ø¹Ù†ÙˆØ§Ù† */}
      <div>
        <label className="text-gray-200 font-medium">Ø§Ù„Ø¹Ù†ÙˆØ§Ù†</label>
        <input
          value={title}
          onChange={(e) => setTitle(e.target.value)}
          className="w-full p-2 mt-1 rounded-lg bg-gray-700 text-white"
          required
        />
      </div>

      {/* Ø§Ù„ÙˆØµÙ */}
      <div>
        <label className="text-gray-200 font-medium">Ø§Ù„ÙˆØµÙ</label>
        <textarea
          value={description}
          onChange={(e) => setDescription(e.target.value)}
          rows={4}
          className="w-full p-2 mt-1 rounded-lg bg-gray-700 text-white"
        />
      </div>

      {/* Ø§Ù„Ù†ÙˆØ¹ */}
      <div>
        <label className="text-gray-200 font-medium">Ø§Ù„Ù†ÙˆØ¹</label>
        <select
          value={type}
          onChange={(e) => setType(e.target.value)}
          className="w-full p-2 mt-1 rounded-lg bg-gray-700 text-white"
        >
          {types.map((t) => (
            <option key={t} value={t}>
              {typesMap[t]}
            </option>
          ))}
        </select>
      </div>

      {/* Ø§Ù„Ø³Ù†Ø© */}
      <div>
        <label className="text-gray-200 font-medium">Ø§Ù„Ø³Ù†Ø©</label>
        <input
          type="number"
          value={year}
          onChange={(e) => setYear(e.target.value)}
          className="w-full p-2 mt-1 rounded-lg bg-gray-700 text-white"
        />
      </div>

      {/* Ø§Ù„ØªØµÙ†ÙŠÙØ§Øª */}
      <div>
        <label className="block mb-1 font-medium text-gray-200">
          Ø§Ø®ØªØ± Ø§Ù„ØªØµÙ†ÙŠÙØ§Øª:
        </label>
        <div className="flex flex-wrap gap-2 max-h-40 overflow-y-auto">
          {categoriesList.map((cat) => (
            <label
              key={cat.id}
              className="flex items-center gap-2 bg-gray-700 px-3 py-1 rounded text-white cursor-pointer"
            >
              <input
                type="checkbox"
                checked={selectedCategories.includes(cat.id)}
                onChange={(e) =>
                  handleCategoryChange(cat.id, e.target.checked)
                }
              />
              {cat.name} ({cat.type})
            </label>
          ))}
        </div>
      </div>

      {/* ØµÙˆØ±Ø© Ø§Ù„ÙƒØ§Ø±Ø¯ */}
      <div>
        <label className="text-gray-200 font-medium">ØµÙˆØ±Ø© Ø§Ù„ÙƒØ§Ø±Ø¯</label>

        {content.card_image && !cardImage && (
          <img
            src={`http://localhost:8000${content.card_image}`}
            className="w-32 h-20 object-cover rounded-lg mb-2"
          />
        )}

        <input
          type="file"
          accept="image/*"
          onChange={(e) => setCardImage(e.target.files?.[0] || null)}
          className="w-full p-2 mt-1 rounded-lg bg-gray-700 text-white"
        />
      </div>

      {/* ØµÙˆØ±Ø© Ø§Ù„Ø¨ÙˆØ³ØªØ± */}
      <div>
        <label className="text-gray-200 font-medium">ØµÙˆØ±Ø© Ø§Ù„Ø¨ÙˆØ³ØªØ±</label>

        {content.poster_image && !posterImage && (
          <img
            src={`http://localhost:8000${content.poster_image}`}
            className="w-32 h-20 object-cover rounded-lg mb-2"
          />
        )}

        <input
          type="file"
          accept="image/*"
          onChange={(e) => setPosterImage(e.target.files?.[0] || null)}
          className="w-full p-2 mt-1 rounded-lg bg-gray-700 text-white"
        />
      </div>

      {/* Ø§Ù„Ø£Ø²Ø±Ø§Ø± */}
      <div className="flex gap-2 justify-end">
        <button
          type="button"
          onClick={onCancel}
          className="px-4 py-2 bg-gray-600 rounded-lg text-white"
        >
          Ø¥Ù„ØºØ§Ø¡
        </button>
        <button
          type="submit"
          disabled={loading}
          className="px-4 py-2 bg-blue-600 rounded-lg text-white"
        >
          {loading ? "Ø¬Ø§Ø±ÙŠ Ø§Ù„Ø­ÙØ¸..." : "Ø­ÙØ¸ Ø§Ù„ØªØ¹Ø¯ÙŠÙ„Ø§Øª"}
        </button>
      </div>
    </form>
  );
}
