"use client";

import { useState, useEffect } from "react";
import { createContent } from "@/services/content.service";
import { getCategories } from "@/services/category.service";

interface FormData {
  type:
    | "movie"
    | "series"
    | "program"
    | "play"
    | "kids"
    | "podcast"
    | "competition";
  title: string;
  description: string;
  year?: number;
  rating?: number;
  is_premium: boolean;
  category_ids: number[];
  poster_image?: File | null;
  card_image?: File | null;
}

interface Category {
  id: number;
  name: string;
  type: string;
}

export default function CreateContentForm({
  onSuccess,
}: {
  onSuccess: () => void;
}) {
  const [form, setForm] = useState<FormData>({
    type: "movie",
    title: "",
    description: "",
    year: undefined,
    rating: undefined,
    is_premium: false,
    category_ids: [],
    poster_image: null,
    card_image: null,
  });

  const [posterPreview, setPosterPreview] = useState<string | null>(null);
  const [cardPreview, setCardPreview] = useState<string | null>(null);
  const [categoriesList, setCategoriesList] = useState<Category[]>([]);

  // Ø¬Ù„Ø¨ Ø§Ù„ØªØµÙ†ÙŠÙØ§Øª
  useEffect(() => {
    const fetchCategories = async () => {
      try {
        const res = await getCategories();
        console.log("categories API response:", res.data);

        // Ø¥Ø°Ø§ ÙƒØ§Ù† res.data.data Ù…ÙˆØ¬ÙˆØ¯ ÙÙ‡Ùˆ Ø§Ù„Ù…ØµÙÙˆÙØ©
        const categories = Array.isArray(res.data.data) ? res.data.data : [];
        setCategoriesList(categories);
      } catch (err) {
        console.error("Fetch categories error:", err);
        setCategoriesList([]);
      }
    };
    fetchCategories();
  }, []);

  const handleChange = (e: any) => {
    const { name, value, type, checked, files } = e.target;

    if (type === "file" && files) {
      const file = files[0];
      setForm({ ...form, [name]: file });

      const reader = new FileReader();
      reader.onload = () => {
        if (name === "poster_image") setPosterPreview(reader.result as string);
        if (name === "card_image") setCardPreview(reader.result as string);
      };
      reader.readAsDataURL(file);
      return;
    }

    let val: any = value;
    if (name === "year" || name === "rating")
      val = value ? Number(value) : undefined;
    if (type === "checkbox") val = checked;

    setForm({ ...form, [name]: val });
  };

  const handleCategoryChange = (id: number, checked: boolean) => {
    if (checked) setForm({ ...form, category_ids: [...form.category_ids, id] });
    else
      setForm({
        ...form,
        category_ids: form.category_ids.filter((c) => c !== id),
      });
  };

  const submit = async (e: any) => {
    e.preventDefault();

    try {
      const formData = new FormData();
      formData.append("type", form.type);
      formData.append("title", form.title);
      formData.append("description", form.description);
      if (form.year !== undefined) formData.append("year", String(form.year));
      if (form.rating !== undefined)
        formData.append("rating", String(form.rating));
      formData.append("is_premium", form.is_premium ? "1" : "0");
      form.category_ids.forEach((id) =>
        formData.append("category_ids[]", String(id))
      );
      if (form.poster_image) formData.append("poster_image", form.poster_image);
      if (form.card_image) formData.append("card_image", form.card_image);

      await createContent(formData);
      onSuccess();

      // Ø¥Ø¹Ø§Ø¯Ø© ØªØ¹ÙŠÙŠÙ† Ø§Ù„Ù†Ù…ÙˆØ°Ø¬
      setForm({
        type: "movie",
        title: "",
        description: "",
        year: undefined,
        rating: undefined,
        is_premium: false,
        category_ids: [],
        poster_image: null,
        card_image: null,
      });
      setPosterPreview(null);
      setCardPreview(null);
    } catch (err: any) {
      console.error("Error adding content:", err);
      alert(err.response?.data?.message || "Ø­Ø¯Ø« Ø®Ø·Ø£ Ø£Ø«Ù†Ø§Ø¡ Ø­ÙØ¸ Ø§Ù„Ù…Ø­ØªÙˆÙ‰.");
    }
  };

  return (
    <form
      onSubmit={submit}
      className="bg-gray-900 p-6 rounded-lg mb-6 shadow-lg text-right"
    >
      <h3 className="text-xl font-bold mb-4 text-red-600">
        â• Ø¥Ø¶Ø§ÙØ© Ù…Ø­ØªÙˆÙ‰ Ø¬Ø¯ÙŠØ¯
      </h3>

      {/* Ø§Ù„Ù†ÙˆØ¹ */}
      <select
        name="type"
        value={form.type}
        onChange={handleChange}
        className="w-full mb-3 p-2 bg-gray-800 rounded text-white"
      >
        <option value="movie">ÙÙŠÙ„Ù…</option>
        <option value="series">Ù…Ø³Ù„Ø³Ù„</option>
        <option value="program">Ø¨Ø±Ù†Ø§Ù…Ø¬</option>
        <option value="play">Ù…Ø³Ø±Ø­ÙŠØ©</option>
        <option value="kids">Ø£Ø·ÙØ§Ù„</option>
        <option value="podcast">Ø¨ÙˆØ¯ÙƒØ§Ø³Øª</option>
        <option value="competition">Ù…Ø³Ø§Ø¨Ù‚Ø©</option>
      </select>

      {/* Ø§Ù„Ø¹Ù†ÙˆØ§Ù† */}
      <input
        name="title"
        placeholder="Ø§Ù„Ø¹Ù†ÙˆØ§Ù†"
        value={form.title}
        onChange={handleChange}
        className="w-full mb-3 p-2 bg-gray-800 rounded text-white"
        required
      />

      {/* Ø§Ù„ÙˆØµÙ */}
      <textarea
        name="description"
        placeholder="Ø§Ù„ÙˆØµÙ"
        value={form.description}
        onChange={handleChange}
        className="w-full mb-3 p-2 bg-gray-800 rounded text-white"
      />

      {/* Ø§Ù„Ø³Ù†Ø© ÙˆØ§Ù„ØªÙ‚ÙŠÙŠÙ… */}
      <div className="grid grid-cols-2 gap-3 mb-3">
        <input
          type="number"
          name="year"
          placeholder="Ø§Ù„Ø³Ù†Ø©"
          value={form.year ?? ""}
          onChange={handleChange}
          className="p-2 bg-gray-800 rounded text-white"
        />
        <input
          type="number"
          step="0.1"
          name="rating"
          placeholder="Ø§Ù„ØªÙ‚ÙŠÙŠÙ…"
          value={form.rating ?? ""}
          onChange={handleChange}
          className="p-2 bg-gray-800 rounded text-white"
        />
      </div>

      {/* Ù…Ø­ØªÙˆÙ‰ Ù…Ø¯ÙÙˆØ¹ */}
      <label className="flex items-center gap-2 mb-3">
        <input
          type="checkbox"
          name="is_premium"
          checked={form.is_premium}
          onChange={handleChange}
        />
        Ù…Ø­ØªÙˆÙ‰ Ù…Ø¯ÙÙˆØ¹
      </label>

      {/* Ø§Ø®ØªÙŠØ§Ø± Ø§Ù„ØªØµÙ†ÙŠÙØ§Øª */}
      <div className="mb-3">
        <label className="block mb-1 font-medium text-gray-200">
          Ø§Ø®ØªØ± Ø§Ù„ØªØµÙ†ÙŠÙØ§Øª:
        </label>
        <div className="flex flex-wrap gap-2 max-h-40 overflow-y-auto">
          {categoriesList.map((cat) => (
            <label
              key={cat.id}
              className="flex items-center gap-2 bg-gray-700 px-3 py-1 rounded text-white cursor-pointer"
            >
              <input
                type="checkbox"
                value={cat.id}
                checked={form.category_ids.includes(cat.id)}
                onChange={(e) => handleCategoryChange(cat.id, e.target.checked)}
              />
              {cat.name} ({cat.type})
            </label>
          ))}
        </div>
      </div>

      {/* ØµÙˆØ± Ø§Ù„Ø¨ÙˆØ³ØªØ± ÙˆØ§Ù„ÙƒØ§Ø±Ø¯ */}
      <div className="grid grid-cols-2 gap-4 mb-3">
        <div>
          <label className="block mb-1">ØµÙˆØ±Ø© Ø§Ù„Ø¨ÙˆØ³ØªØ±</label>
          <input
            type="file"
            name="poster_image"
            accept="image/*"
            onChange={handleChange}
          />
          {posterPreview && (
            <img
              src={posterPreview}
              alt="Poster Preview"
              className="mt-2 w-32 h-20 object-cover rounded"
            />
          )}
        </div>

        <div>
          <label className="block mb-1">ØµÙˆØ±Ø© Ø§Ù„ÙƒØ§Ø±Ø¯</label>
          <input
            type="file"
            name="card_image"
            accept="image/*"
            onChange={handleChange}
          />
          {cardPreview && (
            <img
              src={cardPreview}
              alt="Card Preview"
              className="mt-2 w-32 h-20 object-cover rounded"
            />
          )}
        </div>
      </div>

      <button
        type="submit"
        className="mt-4 w-full bg-red-600 py-2 rounded hover:bg-red-700 transition"
      >
        Ø­ÙØ¸ Ø§Ù„Ù…Ø­ØªÙˆÙ‰
      </button>
    </form>
  );
}
